<template>
  <div>
    <layout-box one="商品管理" two="商品详情">
      <el-form :model="model" label-width="100px">
        <!-- <el-form-item label="分类 :">{{ model.categoryName }}</el-form-item> -->
        <el-form-item label="名称 :">{{ model.name }}</el-form-item>
        <el-form-item label="商品货号 :">{{ model.code }}</el-form-item>
        <el-form-item label="商品标题 :">{{ model.title }} </el-form-item>
        <el-form-item label="规格：">
          <!-- <el-form-item v-for="item in model.items" :key="item" :label="item.keyName + ':'">
            <span v-for="(keyValueItem, i) in item.keyValue" :key="keyValueItem.valueNo">{{ i + 1 }}. {{ keyValueItem.valueName }} </span>
          </el-form-item> -->
          <el-table :data="model.skus" size="mini" border stripe>
            <el-table-column v-for="(item, i) in model.items" :key="i" :label="item.keyName" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.attrSymbolNo[i] }}</span>
              </template>
            </el-table-column>
            <el-table-column label="原价" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.originalPrice }}</span>
              </template>
            </el-table-column>
            <el-table-column label="现价" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.price }}</span>
              </template>
            </el-table-column>
            <el-table-column label="库存" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.stock }}</span>
              </template>
            </el-table-column>
            <el-table-column label="配图" align="center">
              <template slot-scope="scope">
                <el-image :src="scope.row.imgUrl" class="captions_img" fit="contain" />
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item label="主图：">
          <el-image :src="model.imageUrl" style="width:160px ; height:160px" />
        </el-form-item>
        <el-form-item label="视频 :">
          <video :src="model.videoUrl" controls style="width:200px;height:200px" />
        </el-form-item>
        <el-form-item label="商品详情 :" prop="describes">
          <!-- <vue-editor v-model="model.describes" use-custom-image-handler @image-added="handleImageAdded"></vue-editor> -->
          <div v-html="model.describes"></div>
        </el-form-item>
        <el-form-item label="物流方式 :">
          {{ model.logisticsType | formatType }}
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">返回列表</el-button>
        </el-form-item>
      </el-form>
    </layout-box>
  </div>
</template>

<script>
const defaultTypeOptions = [
  {
    label: '包邮',
    value: 0
  },
  {
    label: '按自定义邮费规则',
    value: 1
  },
]
export default {
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      model: {}
    }
  },
  filters: {
    formatType(logisticsType) {
      for (let i = 0; i < defaultTypeOptions.length; i++) {
        if (logisticsType === defaultTypeOptions[i].value) {
          return defaultTypeOptions[i].label
        }
      }
      return '包邮'
    },
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    // 富文本图片上传
    async handleImageAdded (file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      const res = await this.$http.post('/common/oss/uploadFile', formData)
      Editor.insertEmbed(cursorLocation, 'image', res.data.data.webUrl)
      resetUploader()
    },
    async fetch(id) {
      const res = await this.$http.get(
        `/woho/product/head/findById?id=${this.id}&type=2`
      )
      // console.log(res)
      this.model = res.data
      try {
        this.model.describes = JSON.parse(this.model.describes).goodsDetailsContent
      } catch (error) { error }
      console.log(this.model)
    },
    onSubmit() {
      this.$router.push('/commo/list')
    }

  }
}
</script>

<style lang="scss" scoped>
  .captions_img{
    width: 80px;
    height: 80px;
  }
</style>
